import React from 'react';
import SkyLight from 'react-skylight';
import {ComponentValueTypes} from '../Components/ComponentTypes.js';

class ModifyPortValueView extends React.Component {
    constructor(props) {
        super(props);
        this.input = [];
        this.input.view = null;
        this.input.onSave = null;
        this.input.onOpen = null;
    }

    render() {
        if (this.props.port.valueType === ComponentValueTypes.Number) {
            let inputBox = null;

            this.input.view = <input type="number" ref={ref => inputBox = ref} onKeyPress={e => this.checkEnter(e)} />
            this.input.onOpen = function(port) {
                inputBox.value = port.value;
                inputBox.select();
            }
            this.input.onSave = function(port) {
                let value = parseFloat(inputBox.value);

                if (isNaN(value)) {
                    port.setValue(null);
                } else {
                    port.setValue(value);
                }
            }
        } else if (this.props.port.valueType === ComponentValueTypes.String) {
            let inputBox = null;

            this.input.view = <input ref={ref => inputBox = ref} onKeyPress={e => this.checkEnter(e)} />
            this.input.onOpen = function(port) {
                inputBox.value = port.value;
                inputBox.select();
            }
            this.input.onSave = function(port) {
                port.setValue(inputBox.value);
            }
        } else if (this.props.port.valueType === ComponentValueTypes.Reference) {
            this.input.view = <span>References cannot be edited or exported.</span>
            this.input.onOpen = function(port) {

            }
            this.input.onSave = function(port) {

            }
        } else if (this.props.port.valueType === ComponentValueTypes.Bool) {
            let dropdown = null;

            this.input.view = <select ref={ref => dropdown = ref}><option value={true}>true</option><option value={false}>false</option></select>
            this.input.onOpen = function(port) {
                dropdown.value = port.value;
            }
            this.input.onSave = function(port) {
                port.setValue(dropdown.value);
            }
        } else {
            console.log("Modify Port Value: Unknown port type (" + this.props.port.valueType + ")");
        }

        return (
            <SkyLight ref={ref => this.skylight = ref} title="Modify Value">
                <p>Type: {this.props.port.getTypeName()}</p>
                {this.input.view}
                <br /><br />
                <button onClick={() => this.saveButtonClicked()}>Save</button>
            </SkyLight>
        );
    }

    show() {
        this.input.onOpen(this.props.port);
        this.skylight.show();
    }

    saveButtonClicked() {
        this.input.onSave(this.props.port);
        this.skylight.setState({isVisible: false});
    }

    checkEnter(e) {
        if (e.charCode === 13) {
            this.saveButtonClicked();
        }
    }
}

export default ModifyPortValueView;


